/**
 * Reset some basic elements
 */
 body, h1, h2, h3, h4, h5, h6,
 p, blockquote, pre, hr,
 dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}



/**
 * Basic styling
 */
 body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background-color: $background-color;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
    height:100%;

}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
 h1, h2, h3, h4, h5, h6,
 p, blockquote, pre,
 ul, ol, dl, figure,
 %vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}



/**
 * Images
 */
 img {
    max-width: 100%;
    vertical-align: middle;
}



/**
 * Figures
 */
 figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}



/**
 * Lists
 */
 ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
       margin-bottom: 0;
   }
}



/**
 * Headings
 */
 h1, h3, h4, h5, h6 {
    font-weight: $base-font-weight;
}

h2 {
    font-family: 'Raleway', $base-font-family;
    font-weight: 200;
    //color: $secondary-color;
}
h3 {
    font-family: 'Raleway', $base-font-family;
}
.home-header h1 {
    font-family: 'Arvo', $base-font-family;
    font-size:100px;
    margin-bottom: 50px;
}
.home-header h2 {
    font-size:32px;
}
@media screen and (max-width: 375px) {
    .home-header h1 {
        font-family: 'Arvo', $base-font-family;
        font-size: 75px;
        margin-bottom:75px;
    }

    .home-header h2 {
        font-size: 24px;
    }
}
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: $text-color;
}
h1 a:visited, h2 a:visited, h3 a:visited, h4 a:visited, h5 a:visited, h6 a:visited {
    color: $text-color;
}
p {
    font-family: 'Open Sans', $base-font-family;
    font-size: 1.2em;
    line-height:1.7em; 
}
p img {
  display:block;
  margin-left:auto;
  margin-right:auto
}

/**
 * Links
 */
 a {
    color: $brand-color;
    text-decoration: none;

    &:visited {
        color: darken($brand-color, 15%);
    }

    &:hover {
        color: $text-color;
        text-decoration: underline;
    }
}



/**
 * Blockquotes
 */
 blockquote {
    color: $grey-color;
    border-left: 4px solid $grey-color-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    letter-spacing: -1px;
    font-style: italic;

    > :last-child {
        margin-bottom: 0;
    }
}



/**
 * Code formatting
 */
 pre,
 code {
    font-size: 15px;
    border: 1px solid $grey-color-light;
    border-radius: 3px;
    background-color: #eef;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}



/**
 * Wrapper
 */
 .wrapper {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
    max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
        max-width:         calc(#{$content-width} - (#{$spacing-unit}));
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}



/**
 * Clearfix
 */
 %clearfix {

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
 * Icons
 */
 .icon {

    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $grey-color;
        }
    }
}
